<template>
  <v-container>
    <v-layout v-if="loading" justify-center>
      <v-progress-circular indeterminate v-bind:size="70" v-bind:width="7" class="primary--text"></v-progress-circular>
    </v-layout>
    <v-layout row v-if="recipes">
      <v-flex xs4>
        <v-layout class="mb-2" row>
          <v-flex xs12>
            <v-card class="search-bar">
              <v-layout row>
                <v-flex xs10>
                  <v-text-field single-line hide-details placeholder="Cerca" class="fix" v-model="searchText"></v-text-field>
                </v-flex>
                <v-flex xs2>
                  <v-btn icon @click.native="search"><v-icon>search</v-icon></v-btn>
                </v-flex>
              </v-layout>
            </v-card> 
          </v-flex>
        </v-layout>
        <v-card>
          <v-list>
            <v-list-group v-for="item in items" :value="item.active" v-bind:key="item.title">
              <v-list-tile slot="item">
                <!--<v-list-tile-action>
                  <v-icon>{{ item.action }}</v-icon>
                </v-list-tile-action>-->
                <v-list-tile-content>
                  <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                </v-list-tile-content>
                <v-list-tile-action>
                  <v-icon>keyboard_arrow_down</v-icon>
                </v-list-tile-action>
              </v-list-tile>
              <v-list-tile v-for="subItem in item.items" v-bind:key="subItem.title">
                <v-list-tile-content>
                  <v-list-tile-title>{{ subItem.title }}</v-list-tile-title>
                </v-list-tile-content>
                <v-list-tile-action>
                  <v-icon>{{ subItem.action }}</v-icon>
                </v-list-tile-action>
              </v-list-tile>
              </v-list-group>
            </v-list>
          </v-card>
        </v-flex>
        <v-flex xs8 ml-3>
        <h6 v-if="this.$route.params.name" class="white--text">Risultati per "{{ this.$route.params.name }}"</h6>
        <h4 v-else-if="this.$route.params.category" class="white--text">{{ this.$route.params.category }}</h4>
        <v-divider></v-divider>
        <br>
        <span v-if="recipes && recipes.length">
          <v-card v-for="(r, index) in recipes" :key="index" class="item">
            <v-container>
              <v-layout row>
                <v-flex xs3>
                  <v-card-media
                    :src="r.image || '/static/default.jpg'"
                    height="125px"
                  ></v-card-media>                  
                </v-flex>
                <v-flex xs8>
                  <router-link tag="span" style="cursor:pointer" :to="'/recipe/' + r._id">
                    <div class="headline secondary--text">{{ r.name | capitalize }}</div>
                  </router-link>
                  <p>{{ r.description || "" }}</p>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card>
        </span>
        <span v-else height="400">
          <h4 class="white--text">Nessun risultato trovato</h4>
        </span>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      recipes: null,
      loading: false,
      searchText: '',
      items: [
        {
          title: 'Antipasti',
          items: [
            { title: 'Antipasti veloci' },
            { title: 'Pizze e focacce' },
            { title: 'Pane' },
            { title: 'Ricette al forno' },
            { title: 'Finger food' },
            { title: 'Torte salate' },
            { title: 'Insalate' }
          ]
        },
        {
          title: 'Primi',
          items: [
            { title: 'Pasta' },
            { title: 'Pasta fresca' },
            { title: 'Primi veloci' },
            { title: 'Grandi classici' },
            { title: 'Paste sfiziose' },
            { title: 'Gnocchi' },
            { title: 'Riso e cereali' }
          ]
        },
        {
          title: 'Secondi',
          items: [
            { title: 'Facili e veloci' },
            { title: 'Pesce' },
            { title: 'Carne' },
            { title: 'Secondi vegetariani' },
            { title: 'Contorni' },
            { title: 'Piatti unici' },
            { title: 'Fritti' }
          ]
        },
        {
          title: 'Dolci',
          items: [
            { title: 'Biscotti' },
            { title: 'Pasticceria' },
            { title: 'Dolci veloci' },
            { title: 'Torte' },
            { title: 'Cioccolato' },
            { title: 'Torte veloci' },
            { title: 'Marmellate' }
          ]
        }
      ]
    }
  },
  created () {
    this.fetchData()
  },
  methods: {
    fetchData () {
      this.loading = true
      if (this.$route.params.category) {
        axios.get('https://apicookbook.herokuapp.com/recipes/?category=' + this.$route.params.category.toLowerCase())
        .then((result) => {
          this.loading = false
          this.recipes = result.data.data
        })
      } else if (this.$route.params.name) {
        axios.get('https://apicookbook.herokuapp.com/recipes/?name=' + this.$route.params.name.toLowerCase())
        .then((result) => {
          this.loading = false
          this.recipes = result.data.data
        })
      }
    },
    search () {
      if (this.searchText !== '') {
        this.$router.push('/search/name/' + this.searchText.toLowerCase())
      }
    }
  },
  watch: {
    '$route': 'fetchData'
  }
}
</script>

<style scoped>
  .item {
    margin-bottom: 15px;
  }
  .search-bar{
    padding: 5px 10px;
  }
  .fix{
    margin: 10px 0;
  }
  @media screen and (max-width:480px) {
    .item {
      height: 100px;
    }
  }
</style>



