<template>  
  <v-container v-if="recipe">
    <v-parallax :src="recipe.image" height="300">
      <div class="header">
        <h4 class="white--text">{{ recipe.name | capitalize }}</h4>
        <!--<h6 class="white--text">Autore (se c'è)</h6>-->
      </div>
    </v-parallax>
    <br>
    <v-card class="z-depth-1 mb-4">
      <v-container>
        <h6>Ingredienti</h6>  
        <ul>
          <li v-for="(i, index) in recipe.ingredients" :key="index">{{ i }}</li>
        </ul>
      </v-container>
    </v-card>
    <v-stepper v-model="e1" non-linear>
      <v-stepper-header class="white" v-if="recipe.steps.length > 0">

      <span v-for="(s, index) in recipe.steps" :key="index">
        <v-stepper-step :step="index + 1" editable>step {{ index + 1 }}</v-stepper-step>
        <v-divider v-if="index !== recipe.steps.length - 1"></v-divider>
      </span>

      </v-stepper-header>

      <v-stepper-content v-for="(s, index) in recipe.steps" :key="index" :step="index + 1" class="accent">
        <v-card class="gray lighten-1 z-depth-1 mb-5" height="200px">
          <v-container>
            {{ s }}
          </v-container>
        </v-card>
        <v-btn primary @click.native="e1 = (index + 1 !== recipe.steps.length ? index+=2 : 1)">Continue</v-btn>
      </v-stepper-content>
    </v-stepper>
  </v-container>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      e1: null,
      id: null,
      recipe: null
    }
  },
  created () {
    this.id = this.$route.params.id
    axios.get('https://apicookbook.herokuapp.com/recipes/' + this.id)
      .then((result) => {
        this.recipe = {
          _id: result.data._id,
          name: result.data.name,
          ingredients: result.data.ingredients,
          steps: result.data.steps,
          image: result.data.image
        }
      }).catch((error) => {
        console.log(error)
      })
  },
  computed: {
    /*  stepperHeaders () {
      let html = ''
      for (var i = 0; i < this.recipe.steps.length; i++) {
        html += '<div class="stepper__step stepper__step--editable stepper__step--inactive" :step="' + (i + 1) + '"><span class="stepper__step__step">' + (i + 1) + '</span><div class="stepper__label">' + (i + 1) + '</div></div>'
        if (i !== this.recipe.steps.length - 1) {
          html += '<hr class="divider">'
        }
      }
      return html
    } */
  }
}
</script>

<style scoped>
  .header * {
    text-shadow: black 5px 5px 10px;
    position: absolute;
    right: 20px;
  }
  .header h6 {
    font-size: 32px;    
    bottom: 35px;
  }
  .header h4 {
    font-size: 48px;    
    bottom: 80px;
  }
</style>



